import React, { Component, Fragment } from 'react'
import styled from 'styled-components';

const Avatar = styled.div`
    background-image: url('https://s3-us-west-1.amazonaws.com/myavatar/avatar.jpeg');
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 100%;
    height: 12.5em;
    width: 12.5em;
    @media only screen and (max-width: 767px) {
      margin: 0 auto;
    }
`;

const Address = styled.address`
  color: #fff;
  font-style: inherit;
`;

export default class About extends Component {
  render() {
    let resumeData = this.props.resumeData;
    return (
      <Fragment>
        {/* About Section */}
        <section id="about">
          <div className="row">
            <div className="three columns"><Avatar/></div>
            <div className="nine columns main-col">
              <h2>About Me</h2>
              <p>{resumeData.about}</p>
              <div className="row">
                <div className="columns contact-details">
                  <h2>Contact Details</h2>
                  <Address className="address">
                    <span>{resumeData.name}</span><br />
                    <span>{resumeData.address.city}, {resumeData.address.state}
                    </span><br />
                    <a href="tel:+`${resumeData.contact.telephone}`">{resumeData.contact.telephone}</a> <br />
                    <a href="mailto:${resumeData.contact.email}">{resumeData.contact.email}</a><br />
                  </Address>
                </div>
                <div className="columns download">
                  <p>
                    <a href={resumeData.s3.avatar} download className="button"><i className="fa fa-download" />Download Resume</a>
                  </p>
                </div>
              </div> {/* end row */}
            </div> {/* end .main-col */}
          </div>
        </section>
        {/* About Section End*/}
      </Fragment>
    )
  }
}
