import React, { Component, Fragment } from 'react';
import styled from 'styled-components';

const List = styled.ul`
  list-style: none;
`;

const Devicon = styled.i`
  color: #313131;
  font-size: 2em;
`;

export default class Resume extends Component {
  render() {
    {/* Get json */}
    let resumeData = this.props.resumeData;
    {/* Get Work experience items */}
    let Work = resumeData.experience.map(job => (
      <Fragment>
        <div className="row item">
          <div className="twelve columns">
            <h3>{job.company}</h3>
            <p className="info">{job.position} <span>•</span> <em className="date">{job.date}</em></p>
            <List>
              {
                job.achievments.map(achievments => (
                  <li>{achievments}</li>
                ))
              }
            </List>
          </div>
        </div>
        <hr/>
      </Fragment>
    ));
    {/* Get Skills items */}
    let Skills = resumeData.skills.map(skill => (
      <li><span className={skill.className}><em>{skill.skill}</em></span></li>
    ));
    {/* Get Workflow items */}
    let Workflow = resumeData.workflows.map(flow => (
      <li>
        <i class="fa-li fa fa-check"></i>
        {flow.item}
      </li>
    ))
    return (
      <Fragment>
        {/* Resume Section */}
        <section id="resume">
          {/* Education */}
          <div className="row education">
            <div className="three columns header-col">
              <h1><span>Education</span></h1>
            </div>
            <div className="nine columns main-col">
              <div className="row item">
                <div className="twelve columns">
                  <h3>{resumeData.education.name}, <i>{resumeData.education.location}</i></h3>
                  <p className="info">{resumeData.education.studies} <span>•</span> <em className="date">{resumeData.education.date}</em></p>
                  <p>{resumeData.education.about}<a href={resumeData.education.certification} target="_blank"> <br/>
                  {resumeData.education.certificationName}</a>
                  </p>
                </div>
              </div> {/* item end */}
            </div> {/* main-col end */}
          </div> {/* End Education */}
          {/* Work */}
          <div className="row work">
            <div className="three columns header-col">
              <h1><span>Work</span></h1>
            </div>
            <div className="nine columns main-col">
              {Work}
            </div> {/* main-col end */}
          </div> {/* End Work */}
          {/* Skills  List Items */}
          <div className="row skill">
            <div className="three columns header-col">
              <h1><span>Skills</span></h1>
            </div>
            <div className="nine columns main-col">
              <div className="bars">
                <ul className="skills">
                  {/* Skills List Items*/}
                  {Skills}
                </ul>
              </div>{/* end skill-bars */}
            </div> {/* main-col end */}
            <div className="row">
            <div class="three columns header-col"><h1><span>Workflow</span></h1></div>
              <div class="nine columns main-col">
                <ul class="fa-ul mb-0">
                  {/* Workflow List Items */}
                  {Workflow}
                </ul>
              </div>
            </div>
          </div> {/* End skills */}
        </section> {/* Resume Section End*/}
      </Fragment>
    )
  }
}
